import Log from 'hefan-debug-log-s'

const CODE = {
    PARAMS_NULL: 400,   //
    UNAUTHORIZED: 401,   //
    PARAMS_ERROR: 402,   //
    OPERATE_ERROR: 403,  //
    CREATE_ERROR: 501,  //
    FIND_ERROR: 502,  //
    UPDATE_ERROR: 503,  //
    DEL_ERROR: 504,  //
}
const POST_CODE = [CODE.CREATE_ERROR, CODE.FIND_ERROR, CODE.UPDATE_ERROR, CODE.DEL_ERROR]


export default {

    paramsNull(msg = '') {
        return {
            code: CODE.PARAMS_NULL,
            data: null,
            msg
        }
    },
    paramsError(msg = '') {
        return {
            code: CODE.PARAMS_ERROR,
            data: null,
            msg
        }
    },
    createError(msg = '') {
        return {
            code: CODE.CREATE_ERROR,
            data: null,
            msg
        }
    },
    findError(msg = '') {
        return {
            code: CODE.FIND_ERROR,
            data: null,
            msg
        }
    },
    updateError(msg = '') {
        return {
            code: CODE.UPDATE_ERROR,
            data: null,
            msg
        }
    },
    delError(msg = '') {
        return {
            code: CODE.DEL_ERROR,
            data: null,
            msg
        }
    },
    operateError(msg = '') {
        return {
            code: CODE.OPERATE_ERROR,
            data: null,
            msg
        }
    },
    Unauthorized(msg = '') {
        return {
            code: CODE.UNAUTHORIZED,
            data: null,
            msg
        }
    },
    /**
     * codeerror
     * @param {Object} ctx koa
     * **/
    postError (req,res,data) {
        let {code,msg} = data||{}
        if(POST_CODE.includes(code) && req.path != '/api/postDebug'){
            Log.error(`ERROR: ${req.method} ${req.hostname+req.path} ${data.msg}`)
        }
    },

    serverError(res, err, code = '500') {
        res.set('Content-Type', 'text/plain;charset=utf-8');
        res.status(code).end(err + '\n');
    }
}


